.modal_book {
  z-index: 99;
  position: relative;
  padding-top: 40px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 40px;
  max-width: 335px;
  background-color: var(--dark-border-n-text-color);
  border-radius: 18px;
  border: 2px solid var(--light-border-n-text-color);
  overflow: scroll-y;
  display: flex;
  flex-direction: column;
  max-height: 97%;

  @media screen and (min-width: 768px) {
    max-width: none;
    width: 579px;
    padding: 40px;
  }
}

.modal_book-btn-close {
  position: absolute;
  top: 12px;
  right: 12px;

  cursor: pointer;
  margin: 0;
  padding: 0;

  border: none;
  background-color: transparent;

  @media screen and (min-width: 768px) {
    top: 24px;
    right: 24px;
  }

  &:hover .modal_book-svg,
  &:focus .modal_book-svg {
    stroke: var(--light-accent-color);
  }
}

.modal_book-btn-close:hover svg {
  stroke: var(--light-accent-color);
  fill: var(--light-accent-color);
}

.modal_book-detail {
  @media screen and (min-width: 768px) {
    display: flex;
    gap: 24px;
    margin-bottom: 40px;
  }
}

.modal_book-img {
  margin-bottom: 14px;
  width: 100%;
  height: auto;
  max-height: 408px;
  border-radius: 8px;
  overflow: hidden;

  @media screen and (min-width: 768px) {
    min-width: 192px;
    margin-bottom: 0;
    height: 281px;
    width: 192px;
  }
}
.modal_book-info-name {
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.04em;

  @media screen and (min-width: 768px) {
    font-size: 24px;
    line-height: 1.17;
    margin-bottom: 8px;
  }
}

.modal_book-info-author {
  margin-bottom: 18px;
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: -0.04em;
  color: var(--dark-header-secondary-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.29;
  }
}
.modal_book-info-descrip {
  margin-bottom: 14px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: -0.04em;

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
  }
}
.modal_book-btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 8px;
  padding: 12px 26px;
  min-width: 211px;
  min-height: 46px;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.19;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: inherit;

  background-color: transparent;
  border: 2px solid var(--light-accent-color);
  border-radius: 40px;

  cursor: pointer;

  transition: color var(--transition-timing-function),
    background-color var(--transition-timing-function),
    box-shadow var(--transition-timing-function);

  &:hover,
  &:focus {
    color: var(--light-header-bg-color);
    background-color: var(--light-accent-color);
  }

  &:focus {
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.15);
  }

  @media screen and (min-width: 768px) {
    width: 499px;
    height: 64px;
    padding: 20px 100px;
  }
}

.modal_book-icons {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    margin-bottom: 0;
  }
}
.modal_book-one-icon {
  &:hover,
  &:focus {
    filter: grayscale(0) drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.25));
  }
}
.modal_book-svg {
  stroke: var(--light-border-n-text-color);
}

.modal-icons {
  filter: grayscale(100%);
}
.modal_book-note {
  margin-left: auto;
  margin-right: auto;
  width: 242px;
  height: 36px;
  font-family: italic;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 1, 2;

  text-align: center;
  letter-spacing: -0.04em;

  color: var(--dark-header-secondary-color);
}
