body {
  font-family: 'DM Sans bold', sans-serif;
  background-color: var(--primary-background-color);
}

//   

h1,
h2,
h3,
h4,
p,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding-left: 0;
  list-style: none;
}

input,
textarea {
  outline: none;
}

a,
button {
  text-decoration: none;
  cursor: pointer;
}



img {
  display: block;
  max-width: auto;
  height: auto;
}

//  
.hiden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

//  
@media screen and (min-width: 320px) {
  .container {
    display: flex;
    flex-direction: row;
    max-width: 375px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}
